import uniPopup from '@/components/uni-popup/uni-popup.vue';
import wPicker from '../../../components/w-picker/w-picker.vue';
let _self;
export default {
	data() {
		return {
			isok: true,
			lay: false
		};
	},
	components: {
		uniPopup,
		wPicker
	},
	onLoad() {
		_self = this;
		uni.login({
			success: e => {
				console.log(e);
			}
		});

		uni.checkSession({
			success: e => {
				console.log(e);
			}
		});
	},
	onShow() {
		_self.openTimePop();
	},
	methods: {
		openTimePop() {
			_self.$refs.time.show()

			// this.$refs.picker.show();
		}
	}
};
